<template>
    <div>
        <h5>
            Spazio libero
        </h5>
        <hr />
        <div class="row">
            <div v-for="server in freespace" :key="server.name" class="col-sm-6 serverspace">
                <h6>{{ server.name }}</h6>
                <label>{{ server.free }} disponibili su {{ server.total }}</label>
                <div class="progress">
                    <div class="progress-bar" :class="{ 
            'bg-secondary': server.perc == 0,
            'bg-danger': server.perc > 90,
            'bg-warning': server.perc > 75,
            'bg-success': server.perc > 0
            }" role="progressbar" :style="{width: server.perc + '%'}" :aria-valuenow="server.perc" aria-valuemin="0" aria-valuemax="100">
                        <b>{{ server.perc }}% utilizzato</b>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: ["freespace"]
}
</script>